@use 'base/variables'as v;
@use 'base/mixins'as m;

.stores {
    display: flex;
    flex-direction: column;
    justify-content: center;

    &__heading,
    &__text {
        text-align: center;
    }

    &__heading {
        color: v.$secondary;
        margin: 2rem 0 0 0;
    }

    &__text {
        color: v.$black;
        margin-bottom: 3rem;
    }

    &__grid {
            @include m.grid(2, 2rem)
    }

    &__link{
        transition: transform .3s ease;
        &:hover{
            transform: scale(1.1);
        }
    }
}